.works{
    background-color: crimson;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .arrow{
        font-size: 100px;
        position: absolute;
        color: cornsilk;
        cursor: pointer;

        &.left{
            left: 100px;
        }
        &.right{
            right: 100px;
        }

    }

    .slider{
        height: 50%;
        display: flex;
        position: absolute;
        left: 0;
        transition: all 0.5s ease-out;

        .container{
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;

            .item{
                width: 50%;
                height: 100%;
                background-color: white;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;
                
                .left{
                    flex: 4;
                    height: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .leftContainer{
                        width: 90%;
                        height: 100%;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;

                        .imgContainer{
                            width: 100px;
                            height: 110px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            scrollbar-width: none;
                            &::-webkit-scrollbar{
                                display: none;
                            }

                            img{
                                width: 100px;
                            }
                        }
                        h2{
                            font-size: 24px;
                            padding-bottom: 10px;
                            scrollbar-width: none;
                            &::-webkit-scrollbar{
                                display: none;
                            }
                        }
                        h3{
                            font-size: 22px;
                            padding-bottom: 10px;
                            scrollbar-width: none;
                            &::-webkit-scrollbar{
                                display: none;
                            }
                        }
                        p{
                            font-size: 18px;
                            padding-bottom: 10px;
                            scrollbar-width: none;
                            &::-webkit-scrollbar{
                                display: none;
                            }
                        }
                    }
                }
                .right{
                    flex: 8;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    img{
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }
    }
}